section#portfolio.layout.layout-portfolio {
	padding: 20px 0;
}

section#portfolio.layout.layout-portfolio .container {
	width: 95%;
	margin: 0 auto;
	max-width: 1440px;
	position: relative;
}

section#portfolio.layout.layout-portfolio .portfolio-grid {
	width: 95%;
	max-width: 400px;
	margin: 0 auto;
	padding: 25px 0;
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .product-card {
	background-color: transparent;
	width: 100%;
	height: 275px;
	margin: 2%;
	perspective: 1000px;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner {
	position: relative;
	width: 100%;
	height: 100%;
	text-align: center;
	transition: transform 0.8s;
	transform-style: preserve-3d;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-front, section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back {
	position: absolute;
	width: 100%;
	height: 100%;
	backface-visibility: hidden;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-front {
	background-image: url("/dist/img/straws.jpg");
	background-size: cover;
	border-radius: 50%;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back {
	background-color: goldenrod;
	color: purple;
	border-radius: 50%;
	transform: rotateY(180deg);
}

section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back .prod_name {
	font-size: 1.5em;
	margin: 6% auto;
	padding: 2%;
	text-align: center;
	width: 100%;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back .prod_text {
	width: 75%;
	margin: 5% auto;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back .prod_bumpers {
	width: 95%;
	margin: 2% auto;
	padding: 2%;
	display: flex;
	justify-content: space-between;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back .prod_bumpers .price {
	font-size: 3em;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back .prod_bumpers .heart {
	width: 50px;
	height: 50px;
	border: 2px solid purple;
	border-radius: 50%;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back .prod_bumpers .heart:hover {
	background-color: purple;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back .prod_button {
	width: 150px;
	padding: 2%;
	background-color: purple;
	color: goldenrod;
	font-size: 1.1em;
	border-style: none;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back .prod_button:hover {
	cursor: grabbing;
}

section#portfolio.layout.layout-portfolio .portfolio-grid .product-card:hover .product-card-inner {
	transform: rotateY(180deg);
}

@media (min-width: 767px) {
	section#portfolio.layout.layout-portfolio .portfolio-grid {
		max-width: none;
		flex-direction: row;
		flex-wrap: wrap;
	}
	section#portfolio.layout.layout-portfolio .portfolio-grid .product-card {
		width: 44%;
	}
}

@media (min-width: 900px) {
	section#portfolio.layout.layout-portfolio .portfolio-grid {
		max-width: 1000px;
	}
