	}
	section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back .prod_name {
		font-size: 1.2em;
	}
	section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back .prod_text {
		font-size: .9em;
	}
	section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back .prod_bumpers {
		width: 50%;
	}
	section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back .prod_bumpers .price {
		font-size: 1.5em;
	}
	section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back .prod_bumpers .heart {
		width: 25px;
		height: 25px;
	}
	section#portfolio.layout.layout-portfolio .portfolio-grid .product-card .product-card-inner .product-card-back .prod_button {
		width: 100px;
		font-size: .8em;
